const Icon = ({ content }) => {
  if (content.is_done) {
    return (
      <ListItemAvatar>
        <CheckCircleIcon
          style={{ color: "#06bfad", backgroundColor: "white" }}
        />
      </ListItemAvatar>
    );
  } else {
    switch (content.type) {
      case "video":
        return (
          <ListItemAvatar>
            <PlayCircleOutlineIcon />
          </ListItemAvatar>
        );
      case "reading":
        return (
          <ListItemAvatar>
            <ImportContactsIcon />
          </ListItemAvatar>
        );
      case "quiz":
        return (
          <ListItemAvatar>
            <AssignmentIcon />
          </ListItemAvatar>
        );
      case "assignment":
        return (
          <ListItemAvatar>
            <AssignmentIcon />
          </ListItemAvatar>
        );
      default:
        return null;
    }
  }
};

