function CourseOverview(props) {
  let { course } = useParams();

  const success = () => {
    console.log("success");
  };

  const failed = () => {
    props.history.push("/home");
  };

  useEffect(() => {
    props.fetchCourseData(course, success, failed);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (
    Object.keys(props.courses.courseData).length > 0 &&
    props.courses.courseData.constructor === Object
  ) {
    console.log(props.courses.courseData);
    let arr = [];
    for (let i = 0; i < props.courses.courseData.modules.length; ++i) {
      let done = false;
      for (
        let j = 0;
        j < props.courses.courseData.modules[i].contents.length;
        ++j
      ) {
        if (props.courses.courseData.modules[i].contents[j].is_done) {
          done = true;
        } else {
          done = false;
          break;
        }
      }
      if (done) {
        arr.push(props.courses.courseData.modules[i].id);
      } else {
        break;
      }
    }
    console.log(arr);
    return <Overview courses={props.courses} moduleDone={new Set(arr)} />;
  } else {
    return (
      <div className="loader">
        <CustomLoader />
      </div>
    );
  }
}

export default withRouter(CourseOverview);
